@media screen and (min-width: 320px) {
  .price__services {
    font-size: 17px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 24px;
    color: #7E7E82;
    padding: 16px;
    background-color: #fff;
    border-top: 1px solid #D9FFF5; }
  .price__slider {
    display: flex;
    width: 1350px; }
  .price__items {
    width: 260px;
    min-height: 220px;
    background-color: #fff;
    padding: 16px;
    padding-bottom: 0;
    border-radius: 6px;
    margin: 16px;
    list-style-type: none; }
  .price__item {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column; }
  .price__color-text {
    color: #7E7E82; }
  .price__item-text {
    padding-bottom: 16px; }
  .price .text-flex {
    display: flex;
    justify-content: space-between; }
  .price__button {
    border: 0;
    background-color: #FF3E79;
    color: #fff;
    padding: 4px 12px;
    padding-right: 24px;
    border-radius: 16px;
    position: relative;
    text-transform: uppercase; }
    .price__button:after {
      content: '';
      width: 5px;
      height: 5px;
      display: block;
      position: absolute;
      right: 10px;
      top: 40%;
      transform: rotate(45deg);
      border-top: 2px solid #D9FFF5;
      border-right: 2px solid #D9FFF5; } }
