@media screen and (min-width: 320px) {
  .menu {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    z-index: 4;
    background-color: #fff;
    transform: translateX(-100%);
    opacity: 0;
    transition: all .5s ease; }
    .menu__list {
      margin-top: 100px; }
    .menu__item {
      position: relative; }
    .menu__item:hover:before {
      content: '';
      width: 6px;
      height: 32px;
      background-color: red;
      position: absolute;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
      left: -2px;
      background-color: #41F6D7; }
    .menu__link {
      text-decoration: none;
      color: #000;
      display: inline-block;
      padding: 4px 5px 24px 24px;
      font-weight: 500;
      transition: all .5s ease; }
    .menu__item .menu__link:hover {
      color: #7E7E82; }
    .menu__contacts {
      padding-left: 24px; }
    .menu__mail {
      padding-top: 16px; }
    .menu__tell {
      font-weight: 700;
      padding: 12px 0 8px 0; }
    .menu__lang {
      padding-bottom: 32px; }
      .menu__lang--activ {
        color: #7E7E82; }
      .menu__lang-btn {
        border: 0;
        background-color: transparent;
        font-weight: 500; }
        .menu__lang-btn:nth-child(2) {
          padding: 0 7px; }
  .header.open ~ .menu {
    transform: translateX(0);
    opacity: 1; } }

@media screen and (min-width: 768px) {
  .menu {
    width: 320px;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0 0 10px 10px #0E185033;
    z-index: 3; } }

