@media screen and (min-width: 320px) {
  .header {
    background-color: #fff; }
    .header__list {
      display: flex;
      list-style-type: none;
      padding: 24px 16px;
      display: flex;
      justify-content: space-between; }
    .header__item {
      display: flex; }
    .header__item-index {
      position: relative;
      z-index: 5; }
    .header .burger {
      border: 0;
      position: relative;
      cursor: pointer; }
      .header .burger__stripe {
        height: 2px;
        background-color: #fff;
        border-radius: 5px;
        position: absolute;
        left: 10px;
        transition: all .5s ease; }
      .header .burger__stripe:nth-child(1) {
        width: 18px;
        transform: translateY(-7px); }
      .header .burger__stripe:nth-child(2) {
        width: 10px; }
      .header .burger__stripe:nth-child(3) {
        width: 14px;
        transform: translateY(7px); }
      .header .burger.open__burger .burger__stripe:nth-child(2) {
        opacity: 0; }
      .header .burger.open__burger .burger__stripe:nth-child(1) {
        width: 21px;
        transform: rotate(45deg); }
      .header .burger.open__burger .burger__stripe:nth-child(3) {
        width: 21px;
        transform: rotate(-45deg); }
    .header .burger__none {
      display: none; }
    .header__cps {
      width: 95px;
      margin-left: 16px;
      margin-right: 4vw; }
    .header__search {
      opacity: 0;
      -webkit-animation: ani 1s forwards;
              animation: ani 1s forwards;
      background-image: url(./static/search.svg);
      background-repeat: no-repeat;
      background-position: 50%;
      display: none;
      position: fixed;
      right: 20px; }
    .header .open__search {
      width: 320px; }
    .header__item.open__search .header__search {
      display: block; }
    .header__item-none {
      display: none; }
    .header__line::before {
      content: '';
      width: 2px;
      height: 32px;
      background-color: #EAEAEA;
      display: block;
      margin-left: -65px;
      margin-top: 5px; }
    .header__link {
      width: 60px;
      display: flex;
      justify-content: space-around;
      opacity: 0;
      -webkit-animation: ani 1s forwards;
              animation: ani 1s forwards; }
    .header__link--none {
      display: none; }
    .header .img-repair {
      background-image: url(./static/repair.svg);
      background-repeat: no-repeat;
      background-position: 50%; }
    .header .img-icon {
      background-image: url(./static/icon.svg);
      background-repeat: no-repeat;
      background-position: 50%; } }

@media screen and (min-width: 768px) {
  .header .open__search {
    width: 320px; }
  .header__decorative {
    width: 2px;
    height: 32px;
    background-color: #EAEAEA;
    display: block;
    margin-left: 20px;
    margin-top: 5px; }
  .header__decorative--none {
    opacity: 0;
    transition: all 1s ease; }
  .header__search {
    position: static; }
  .header__item .contact {
    position: absolute;
    display: flex;
    right: 150px; }
  .header__item-none {
    display: flex; }
  .header__link--none {
    display: flex; } }
