* {
  padding: 0;
  margin: 0;
  box-sizing: border-box; }

body {
  font-family: 'TT Lakes', sans-serif;
  background-color: #F8F8F8;
  color: #000;
  overflow-x: hidden;
  overflow-y: scroll; }

.circle-pn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: inline-block;
  background-color: #FF3E79; }

@-webkit-keyframes ani {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes ani {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.arrow {
  width: 20px;
  height: 20px;
  display: inline-block;
  position: relative; }
  .arrow::before {
    content: '';
    width: 5px;
    height: 5px;
    transform: rotate(225deg);
    position: absolute;
    left: 0;
    border-left: 2px solid #41F6D7;
    border-top: 2px solid #41F6D7;
    transition: all .5s ease;
    top: 10px; }
  .arrow::after {
    content: '';
    width: 5px;
    height: 5px;
    transform: rotate(225deg);
    position: absolute;
    left: 0;
    border-left: 2px solid #41F6D7;
    border-top: 2px solid #41F6D7;
    transition: all .5s ease;
    top: 17px; }
