@-webkit-keyframes text {
  0% {
    height: 0;
    opacity: 0; }
  100% {
    height: 154px;
    opacity: 1; } }

@keyframes text {
  0% {
    height: 0;
    opacity: 0; }
  100% {
    height: 154px;
    opacity: 1; } }

@-webkit-keyframes text-open {
  0% {
    height: 60px; }
  100% {
    height: 148px; } }

@keyframes text-open {
  0% {
    height: 60px; }
  100% {
    height: 148px; } }

@media screen and (min-width: 320px) {
  .services__heading {
    font-weight: 700;
    font-size: 28px;
    margin: 24px 0;
    padding-left: 12px; }
  .services__scroll {
    overflow-x: scroll;
    overflow-y: hidden;
    height: 50px;
    padding-top: 10px; }
  .services__list {
    display: flex;
    flex-wrap: nowrap;
    list-style-type: none;
    width: 1200px;
    margin-left: 14px; }
  .services__link {
    text-decoration: none;
    color: #7E7E82;
    padding: 8px 10px 8px 16px;
    border-radius: 5px;
    border: 2px solid transparent; }
    .services__link:hover {
      border: 2px solid #B8FFEC;
      background-color: #fff; }
  .services__info {
    margin-top: 24px; }
  .services__text {
    padding: 0 16px; }
  .services__text.open--text .arrow::after, .services__text.open--text .arrow::before {
    transform: rotate(45deg); }
  .services__paragraph:nth-child(2) {
    padding-top: 16px;
    height: 0px;
    overflow: hidden; }
  .services__text.open--text .services__paragraph:nth-child(2) {
    -webkit-animation: text 1s forwards alternate;
            animation: text 1s forwards alternate; }
  .services__button {
    border: 0;
    background-color: transparent;
    font-size: 16px;
    margin: 16px 0; }
  .services .img-cps {
    width: 100%; } }

@media screen and (min-width: 768px) {
  .services__info {
    display: flex;
    padding: 0 24px; }
  .services__text {
    width: 50%; }
  .services__paragraph:nth-child(2) {
    height: 60px; }
  .services__text.open--text .services__paragraph:nth-child(2) {
    -webkit-animation: text-open 1s forwards alternate;
            animation: text-open 1s forwards alternate; }
  .services__img {
    width: 360px; }
  .services .img-cps {
    width: calc(360px + 10vw);
    border-radius: 10px; } }
