@media screen and (min-width: 320px) {
  .types__repair {
    font-size: 17px;
    font-weight: 400;
    text-transform: uppercase;
    line-height: 24px;
    color: #7E7E82;
    padding: 16px;
    background-color: #fff;
    border-top: 1px solid #D9FFF5; }
  .types__slider {
    width: 1200px;
    padding: 16px;
    list-style-type: none;
    display: flex; }
  .types .link {
    width: 240px;
    height: 160px;
    background-color: #fff;
    border: 1px solid #EAEAEA;
    display: flex;
    justify-content: space-between;
    padding: 0 16px;
    margin-right: 16px;
    border-radius: 6px; }
    .types .link__text {
      margin-top: 16px;
      font-weight: 500; }
    .types .link__arrow {
      width: 40px;
      height: 40px;
      border: 2px solid #FF3E79;
      border-radius: 50%;
      position: relative;
      align-self: flex-end;
      margin-bottom: 16px; }
    .types .link__arrow::after {
      content: '';
      width: 7px;
      height: 7px;
      border-left: 2px solid #FF3E79;
      border-top: 2px solid #FF3E79;
      border-top-right-radius: 2px;
      border-bottom-left-radius: 2px;
      position: absolute;
      transform: rotate(130deg);
      left: 13px;
      top: 13px; }
  .types__circle {
    padding: 16px; }
  .types .swiper-pagination-bullet {
    width: 12px;
    height: 12px; }
    .types .swiper-pagination-bullet-active {
      background-color: #7E7E82; } }

@media screen and (min-width: 768px) {
  .types__slider {
    width: 100%;
    height: 180px;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(3, 230px);
    gap: 32px; } }

@media screen and (min-width: 1120px) {
  .types__slider {
    grid-template-columns: repeat(4, 230px); } }
